export const storeSummarizeTransaction = params => { return axios.get('/admin/store/summarize/transaction', { params: params }) }
// 
export const storeSummarizeService = params => { return axios.get('/admin/store/summarize/service', { params: params }) }

// 
export const storeSummarizePromote = params => { return axios.get('/admin/store/summarize/promote', { params: params }) }
// 
export const storeAccountInfoDetection = params => { return axios.get('/admin/store/storeAccountInfo/detection', { params: params }) }
// 
export const storeMessageAllpage = params => { return axios.get('/admin/store/message/allpage', { params: params }) }
// 
export const storeMessageUnread = params => { return axios.get('/admin/store/message/unread', { params: params }) }




