// -
import axios from './../fetch'

// area
export const areaTreeArea = params => { return axios.get('/admin/store/area/tree', { params: params }) }
// 
export const areaRegionList = params => { return axios.get('/admin/store/area/list', { params: params }) }

// 
export const areaRegionGet = params => { return axios.get('/admin/store/area/get', { params: params }) }

// code
export const areaRegionGetAreaCode = params => { return axios.get('/admin/store/area/getAreaCode', { params: params }) }

// 
export const areaRegionSave = params => { return axios.post('/admin/store/area/save', params) }

// 
export const areaRegionDelete = params => { return axios.post('/admin/store/area/delete', params) }

// 
export const areaRegionUpdate = params => { return axios.post('/admin/store/area/update', params) }

// 
export const areaRegionSort = params => { return axios.post('/admin/store/area/sort', params) }



