// 
import axios from '../fetch'

// 
export const brandList = params => { return axios.get('/admin/store/brand/page', { params: params }) }

// 
export const brandUpdate = params => { return axios.post('/admin/store/brand/update', params) }

// 
export const brandDelete = params => { return axios.post('/admin/store/brand/delete', params) }

// 
export const brandSave = params => { return axios.post('/admin/store/brand/save', params) }

// 
export const brandGet = params => { return axios.get('/admin/store/brand/get', { params: params }) }
// 
export const brandCommit = params => { return axios.get('/admin/store/brand/commit', { params: params }) }

// 
export const cancelCommit = params => { return axios.get('/admin/store/brand/quit', { params: params }) }
// 
export const brandSort = params => { return axios.post('/admin/store/brand/sort', params) }

//  
export const brandListAll = params => { return axios.get('/admin/store/brand/list', { params: params }) }



