// 
import axios from 'axios'
import cmsMessage from '@/components/cms/cmsMessage'
import router from './../router/index'
/**
 * 
 */
const $axios = axios.create({
  baseURL: process.env.baseUrl,
  timeout: 500000,
  headers: {
    'JSPGOU-Auth-Token': localStorage.getItem('storeKey'),
    'Redirect-Header': false
  }
})
/**
 * 
 */
$axios.interceptors.request.use(config => { // 
  config.headers['JSPGOU-Auth-Token'] = localStorage.getItem('storeKey')
  config.headers['Redirect-Header'] = false
  return config
}, error => {
  console.log(error)
  Promise.reject(error) // 
})

/**  */
$axios.interceptors.response.use(response => {
  const code = response.data.code
  if (response.data.token && response.data.token !== '') {
    localStorage.setItem('storeKey', response.data.token)
  }
  if (code < 10000 && code !== 200 && code !== 100) {
    // 
    // const $zoneEl = document.querySelector('#zoneDialog')
    // if ($zoneEl) {
    //   document.body.removeChild($zoneEl)
    // }
    if (code === 501 || code === 502 || code === 503 || code === 506) {
      localStorage.setItem('storeKey', '')
      router.push('/login')
    }
    cmsMessage({
      message: response.data.message,
      type: 'error',
      timer: '1000'
    })
  }

  return response.data
},
error => {
  console.log(error.response)
  cmsMessage({
    message: '',
    type: 'error',
    timer: '1500'
  })
  return Promise.reject(error)
}
)
export default $axios



