export default {
  required: (mes = '', trigger = 'blur') => { // 
    return {
      required: true,
      message: mes,
      trigger: trigger
    }
  },
  $length: (len = 1, trigger = 'blur') => { // 
    return {
      len: len,
      message: '' + len,
      trigger: trigger
    }
  },
  len: (min = 1, max = 255, trigger = 'blur') => { // 
    return {
      min: min,
      max: max,
      message: '' + min + '-' + max,
      trigger: trigger
    }
  },
  min: (min = 1, message = `${min}`, trigger = 'blur') => {
    return {
      min: min,
      message: message,
      trigger: trigger
    }
  },
  max: (max = 255, message = `${max}`, trigger = 'blur') => {
    return {
      max: max,
      message: message,
      trigger: trigger
    }
  },
  type: (type = 'string', message = `${type}`, trigger = 'blur') => {
    return {
      type: type,
      message: message,
      trigger: trigger
    }
  },

  password: (callback) => {
    return {
      validator: callback,
      trigger: 'blur'
    }
  },
  custom: (callback, trigger = 'blur') => {
    return {
      validator: callback,
      trigger: trigger
    }
  },
  email: () => {
    const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    const va = (rule, value, callback) => {
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      validator: va,
      trigger: 'blur'
    }
  },
  english: () => {
    const reg = /^\w+$/
    const va = (rule, value, callback) => {
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(','))
      }
    }
    return {
      validator: va,
      trigger: 'blur'
    }
  },
  capital: () => { // 
    const reg = /^[0-9A-Z]*$/
    const va = (rule, value, callback) => {
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      validator: va,
      trigger: 'blur'
    }
  },
  phone: () => { // 
    const reg = /^1[3456789]\d{9}$/
    const va = (rule, value, callback) => {
      if (value === '') {
        callback()
      }
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      validator: va,
      trigger: 'blur'
    }
  },
  tel: () => { // 
    const reg = /^((\d{3,4}-\d{7,8})|(1[3456789]\d{9}))$/
    const va = (rule, value, callback) => {
      if (value === '') {
        callback()
      }
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      validator: va,
      trigger: 'blur'
    }
  },
  float: (message = '', trigger = 'blur') => {
    const reg = /^[0-9]+([.]{1}[0-9]+){0,1}$/
    const va = (rule, value, callback) => {
      if (value === null || value === '') {
        callback()
      }
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(message))
      }
    }
    return {
      validator: va,
      trigger: trigger
    }
  },
  // 
  decimal: (message = '', trigger = 'blur') => {
    const reg = /^(([0-9]*)|(([0]\.\d{1,2}|[0-9]*\.\d{1,2})))$/
    const va = (rule, value, callback) => {
      if (value === null || value === '') {
        callback()
      }
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(message))
      }
    }
    return {
      validator: va,
      trigger: trigger
    }
  },
  //
  number: (message = '', trigger = 'blur') => {
    const reg = /^[0-9]\d*$/
    const va = (rule, value, callback) => {
      if (value === '') {
        callback()
      }
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(message))
      }
    }
    return {
      validator: va,
      trigger: trigger
    }
  },
  integer: (message = '0', trigger = 'blur') => {
    const reg = /^[1-9]\d*$/
    const va = (rule, value, callback) => {
      if (value === '') {
        callback()
      }
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(message))
      }
    }
    return {
      validator: va,
      trigger: trigger
    }
  },
  url: (message = '', trigger = 'blur') => {
    const reg = /^(?=^.{3,255}$)(http(s)?:\/\/)?(www\.)?[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(\.[a-zA-Z0-9][-a-zA-Z0-9]{0,62})+(:\d+)*(\/\w+\.\w+)*$/
    const va = (rule, value, callback) => {
      if (value === '') {
        callback()
      }
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error(message))
      }
    }
    return {
      validator: va,
      trigger: trigger
    }
  }
}




