<template>
    <el-dialog title="" :visible.sync="state" width="1090px" top="15vh" :before-close="handleClose" id="zoneDialog">
      <div class="cms-zone-warper">
        <picture-tree @get='getResourcesId'></picture-tree>
        <picture-zone :resource='resource' @change="getCheckedItems" :type='type' :size='size'></picture-zone>
      </div>
    </el-dialog>
</template>
<script>
// changeid
import pictureTree from './fileTree.vue'
import pictureZone from './pictureZone.vue'
export default {
  components: {
    pictureTree,
    pictureZone
  },
  name: 'zoneDialog',
  props: {
    size: {// 
      default: 1,
      type: Number
    },
    type: {// 
      default: 'image',
      type: String
    }
  },
  data() {
    return {
      state: true, // ,
      resource: {// 
        name: '',
        id: ''
      },
      pictureItems: []
    }
  },
  methods: {
    showViewer(item) { // 
      this.$viewer({
        name: item.alias,
        url: item.url
      })
    },
    getResourcesId(obj) { // 
      this.resource = obj
    },
    getCheckedItems(items) {
      // 
      this.pictureItems = items
      this.$emit('change', items)
      this.close()
    },
    close() { // 
      console.log('close')
      this.$nextTick(() => {
        this.state = false
        setTimeout(() => {
          this.$destroy()
          const $zoneEl = document.querySelector('#zoneDialog')
          $zoneEl.parentNode.removeChild($zoneEl)
        }, 5)
      })
    },
    handleClose(done) { // 
      done()
      this.close()
    }
  }
}
</script>

<style scoped lang='scss'>
.cms-zone-warper {
  overflow: hidden;
}

</style>



