// 
import axios from './../fetch'
// 
export const resourceSpaceTree = params => { return axios.get('/admin/store/resourcespace/findAll', { params: params }) }

// 
export const resourceSavePlatform = params => { return axios.post('/admin/store/resourcespace/saveStore', params) }

// 
export const resourceUpdatePlatform = params => { return axios.post('/admin/store/resourcespace/update', params) }

//  
export const resourceDataPage = params => { return axios.get('/admin/store/resourcespaceData/page', { params: params }) }// page
// 
export const resourceDataDeletes = params => { return axios.post('/admin/store/resourcespaceData/deletes', params) }
// 
export const resourceDataUpdateName = params => { return axios.post('/admin/store/resourcespaceData/update', params) }
// 
export const resourceSaveDelete = params => { return axios.post('/admin/store/resourcespace/delete', params) }
// 
export const resourceDataShift = params => { return axios.post('/admin/store/resourcespaceData/shift', params) }
// 
export const platformUpload = (params, callback1, callback2) => {
  return axios({
    url: '/admin/store/upload/o_upload',
    headers: { 'Content-Type': 'multipart/form-data' },
    method: 'post',
    onUploadProgress: function(progressEvent) { // 
      if (progressEvent.lengthComputable) {
        // lengthComputable
        // lengthComputablefalseprogressEvent.totalprogressEvent.loaded
        callback1(progressEvent)
      }
    },
    data: params
  }).then(res => {
    callback2(res)
  })
}



